<template>
  <div class="detail">
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      left-arrow
      @click-left="back"
    />
    <div class="xqtp">
      <img :src="detailData.large_img" />
    </div>

    <div class="guige">
      <div class="names">
        <div class="ms">
          <p class="p1">{{ detailData.name }}</p>
          <p class="p2">{{ detailData.enname }}</p>
        </div>
        <div class="props">￥{{ detailData.price }}</div>
      </div>

      <div class="rules">
        <div class="rule" v-for="(v1, i1) in detailData.rulesData" :key="i1">
          <div class="rule-title">{{ v1.title }}</div>

          <div class="rule-items">
            <div
              :class="{ active: v1.currentIndex === i2 }"
              v-for="(v2, i2) in v1.rule"
              :key="i2"
              @click="selectRule(v1, i2)"
            >
              {{ v2.title }}
            </div>
          </div>
        </div>
      </div>

      <div class="sl">
        <div class="xz">选择数量</div>
        <div class="count">
          <van-stepper theme="round" button-size="22" disable-input />
        </div>
      </div>

      <div class="mss">
        <div class="spms">商品描述</div>
        <div v-for="(item, index) in detailData.desc" :key="index">
          {{ index + 1 }}、{{ item }}
        </div>
      </div>

      <div class="spdh">
        <van-goods-action>
          <van-goods-action-icon
            icon="chat-o"
            text="客服"
            color="#f6c73d"
            to="/kef"
          />
          <van-goods-action-icon icon="cart-o" text="购物车"  :badge="shopbagCount > 0 ? shopbagCount : ''" @click="goShopbag" />
          <van-goods-action-icon icon="star" :text="likeStatus ? '已收藏':'收藏'" @click="linkAnds" :color="likeStatus ? '#f6c73d':'#323233'" />
          <van-goods-action-button type="warning" text="加入购物车" @click="addShopbag" />
          <van-goods-action-button
            type="danger"
            text="立即购买"
            color="#f6c73d"
            @click="buyNow"
          />
        </van-goods-action>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState, mapMutations} from 'vuex';
export default {
  name: "Detail",
  data() {
    return {
      pid: "",

      detailData: {},

      //收藏状态
      likeStatus:false,

      //购物车数量
      count:1
    };
  },
  created() {
    //截取pid
    this.pid = this.$route.params.pid;
    this.xqtp();
    this.links();
  },

  computed: {
    ...mapState(['shopbagCount']),
  },
  methods: {

    ...mapMutations(['changeShopbagCount']),

    //路由返回上一级
    back() {
      this.$router.go(-1);
    },

    selectRule(item, index) {
      if (item.currentIndex === index) {
        return;
      }
      item.currentIndex = index;
    },

    //商品详情获取数据
    xqtp() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/productDetail",
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code === 600) {
            let data = res.data.result[0];

            //处理res
            let rules = ["cream", "tem", "sugar", "milk"];

            //保存处理规格数据
            data.rulesData = [];

            rules.forEach((v) => {
              //如果不存在规格，则不添加到data.rulesData
              if (!data[v]) {
                return;
              }

              let rulesText = data[v].split("/");

              let currentRule = {
                title: data[v + "_desc"],
                currentIndex: 0,
                rule: [],
              };

              rulesText.forEach((item) => {
                let o = {
                  title: item,
                };
                currentRule.rule.push(o);
              });

              data.rulesData.push(currentRule);
            });

            //处理商品描述信息
            data.desc = data.desc.split(/\n/);

            

            this.detailData = data;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //收藏
    link() {

      let tokenString = localStorage.getItem('tokenString');
      

      if(!tokenString ){
        return this.$router.push({name:"Login"});
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/like",
          data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString
          },
      }).then((res) => {
          
          this.$toast.clear();

          //未收藏
          if(res.data.code === 700){
          this.$toast({
            message:res.data.msg,
            forbidClick: true,
            duration: 3000
          })
          return this.$router.push({name:'Login'});
        }

          //已收藏
          if (res.data.code === 800) {

            this.likeStatus = true;

          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
        
    },

    //查询是否收藏
    links(){

      let tokenString = localStorage.getItem('tokenString');
      

      if(!tokenString ){
        return;
      }


      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/findlike",
          params: {
            appkey: this.appkey,
            pid: this.pid,
            tokenString
          },
      }).then((res) => {
          
          this.$toast.clear();

          if(res.data.code == 700){
              return;
        }

          //已收藏
          if (res.data.code === 1000) {

            if(res.data.result.length > 0){
                this.likeStatus = true;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //收藏或者取消收藏
    linkAnds(){
      if(this.likeStatus){
        //取消收藏
          this.linkqx();
      }else{
        this.link();
      } 
    },

    //取消收藏
    linkqx(){
       let tokenString = localStorage.getItem('tokenString');
      

      if(!tokenString ){
        return this.$router.push({name:"Login"});
      }

      this.$toast.loading({
        message: "加载中",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/notlike",
          data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString
          },
      }).then((res) => {
          
          this.$toast.clear();

          //未收藏
          if(res.data.code == 700){
          this.$toast({
            message:Toast.success(res.data.msg),
            forbidClick: true,
            duration: 0
            
          })
          return this.$router.push({name:'Login'});
        }

          if (res.data.code === 900) {
            if(res.data.result === 1){
              //取消收藏
                this.likeStatus = false;
            }
          } else {
            this.$toast({
              message:Toast.success(res.data.msg),
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //选择规格
    selectRule(item, index) {
      //如果点击相同rule
      if (item.currentIndex === index) {
        return;
      }

      item.currentIndex = index;
    },

    //添加购物车和确认购买跳转
    addShopbag(isBuy) {
      let tokenString = localStorage.getItem("tokenString");
      // 

      if (!tokenString) {
        
        return this.$router.push({ name: "Login" });
      }

      let rules = [];
      this.detailData.rulesData.forEach((v) => {
        rules.push(v.rule[v.currentIndex].title);
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/addShopcart",
        data: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString,
          count: this.count,
          rule: rules.join("/"),
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code === 3000) {
            if (res.data.status === 1) {
             
              //提交mutations修改shopbagCount
              this.changeShopbagCount(this.shopbagCount + 1);

              this.$toast({
                message: res.data.msg,
                forbidClick: true,
                duration: 3000,
              });
            }

            if (isBuy) {
              this.$router.push({name: 'Pay', query: {sids: res.data.sid}});
            }
          } else {
            this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });
          }
          
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //跳转购物
    goShopbag() {
      this.$router.push({name:"Guowu"});
    },

    // 立即购买
    buyNow() {
      this.addShopbag(true);
    }
  },
};
</script>

<style lang="less" scoped>
.detail {
  padding-bottom: 400px;
  width: 100%;
  position: relative;
  .xqtp {
    margin-top: 5px;
    img {
      width: 100%;
      //    border-radius: 15px;
    }
  }
  .guige {
    width: 95%;
    background: white;
    position: absolute;
    top: 300px;
    left: 8px;
    border-radius: 15px;
    height: 350px;
    .names {
      width: 95%;
      .ms {
        float: left;
        .p1 {
          display: block;
          font-size: 20px;
          color: #6f768d;
          font-weight: bold;
          margin-left: 15px;
        }
        .p2 {
          display: block;
          width: 100px;
          white-space:nowrap;
          overflow: hidden;
          margin-top: -15px;
          font-size: 16px;
          margin-left: 15px;
        }
      }
      .props {
        float: right;
        color: #f6c73d;
        font-size: 20px;
        margin-top: 20px;
      }
    }
    .rules {
      position: absolute;
      top: 65px;
      left: 15px;
      margin-top: 30px;
    }

    .rule {
      display: flex;
      margin-bottom: 10px;
    }

    .rule-title {
      width: 50px;
      height: 26px;
      line-height: 26px;
      font-size: 18px;
    }

    .rule-items {
      display: flex;
      > div {
        width: 60px;
        text-align: center;
        height: 26px;
        line-height: 26px;
        background-color: #e8e8e8;
        border-radius: 13px;
        color: #666;
        margin-left: 35px;
        font-size: 14px;
        &:last-child {
          margin-right: 0;
        }
        &.active {
          background-color: #f6c73d;
          color: #fff;
          margin-left: 35px;
        }
      }
    }
    .sl {
      width: 90%;
      height: 75px;
      border: 1px solid #e8e8e8;
      border-left: none;
      border-right: none;
      position: absolute;
      top: 180px;
      left: 15px;
      .xz {
        font-size: 18px;
        line-height: 75px;
      }
      .count {
        position: absolute;
        top: 25px;
        right: 15px;
      }

      /deep/ .van-stepper--round .van-stepper__plus {
        background-color: #f6c73d;
      }

      /deep/ .van-stepper--round .van-stepper__minus {
        border: 1px solid #f6c73d;
      }

    }
    .mss {
      position: absolute;
      top: 265px;
      left: 0px;
      padding-left: 15px;
      .spms {
        margin-bottom: 10px;
      }
    }
  }
  /deep/ .van-nav-bar__text{
        color:#f6c73d;
      }
  /deep/ .van-icon-arrow-left::before{
    color:#f6c73d;
  }
}
</style>